<template>
  <div class="home">
    <div v-if="isLoading">...Loading...</div>
    <div v-else-if="hasError">Error</div>
    <template v-else>
      <div v-for="item in getItemsList" :key="item.id" class="item">
        <div>{{ item.title }}</div>
        <div>{{ item.producer }}</div>
        <div>{{ item.price }}</div>
        <div><img :src="item.imgSrc" /></div>
      </div>
    </template>
    <div class="add-form">
      <div>
        <label>
          Title
          <input v-model="product.title" type="text" />
        </label>
      </div>
      <div>
        <label>
          Producer
          <input v-model="product.producer" type="text" />
        </label>
      </div>
      <div>
        <label>
          Price
          <input v-model="product.price" type="number" />
        </label>
      </div>
      <div>
        <label>
          Image src
          <input v-model="product.imgSrc" type="text" />
        </label>
      </div>
      <button @click="onAdd">Додати новий продукт</button>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'

export default {
  name: 'HomePage',
  components: {},

  data() {
    return {
      product: {},
    }
  },

  computed: {
    ...mapGetters('shoppingList', ['getItemsList', 'isLoading', 'hasError']),
  },

  created() {
    this.loadList()
  },

  methods: {
    ...mapActions('shoppingList', ['loadList', 'addItem']),
    onAdd() {
      this.addItem(this.product)
      this.product = {}
    },
  },
}
</script>

<style lang="scss">
.item {
  display: flex;
  align-items: center;
  gap: 15px;
  div > img {
    width: 10%;
    aspect-ratio: 3/4;
    object-fit: contain;
  }
}
</style>
